<template>
    <div>
        <ul class="list-group">
            <div v-for="(item, index) in items_list" :key="index">
                <li
                    class="list-group-item d-flex justify-content-between align-items-center text-primary"
                    @click="handleShowTopicContent(index)"
                >
                    {{
                        item.word && item.word != ""
                            ? item.word
                            : item.pronunciation
                    }}
                    <span class="btn btn-light"
                        ><i
                            :class="`fa-solid fa-chevron-${
                                index === selected_index ? 'up' : 'down'
                            }`"
                        ></i
                    ></span>
                </li>
                <div class="card" v-if="index === selected_index">
                    <div class="card-body bg-light">
                        <p>Pronunciation:</p>
                        <p>
                            <b
                                ><span
                                    class="text-success"
                                    v-html="item.pronunciation"
                                ></span
                            ></b>
                        </p>
                        <p v-if="false">Type:</p>
                        <p v-if="false">
                            <b
                                ><span
                                    class="text-success"
                                    v-html="item.type"
                                ></span
                            ></b>
                        </p>
                        <p>Meaning:</p>
                        <span class="text-success" v-html="item.meaning"></span>
                    </div>
                </div>
            </div>
        </ul>
    </div>
</template>

<script>
import LocalStorage from "@/utils/LocalStorage.js";
import VocabulariesTopics from "@/data/japanese/index.js";

export default {
    data() {
        return {
            topics: null,
            items_list: null,
            selected_index: null,
        };
    },

    async created() {
        await this.init();
    },

    methods: {
        async init() {
            this.localStorage = new LocalStorage();
            this.localStorage.setLanguageKey("japanese-vietnamese");
            /* eslint-disable */
            switch (this.localStorage.language_key) {
                // case "japanese-english":
                //     break;
                // case "japanese-vietnamese":
                //     break;
                default:
                    this.topics = VocabulariesTopics.vietnamese;
                    break;
            }

            this.items_list =
                this.topics[this.localStorage.setting.level.topic].value[
                    this.localStorage.setting.level.level
                ].value[this.localStorage.setting.level.sub_level].value;
        },

        async handleShowTopicContent(selected_index) {
            if (this.selected_index == selected_index) {
                this.selected_index = null;
            } else {
                this.selected_index = selected_index;
            }
        },
    },
};
</script>
